<template>
    <div class="row">
        <div class="col-sm-12 col-md-8">
            <!--添加新品-->
        <newpizza></newpizza>
        </div>
        <div class="col-sm-12 col-md-4">
             <!--品种展示与删除-->
            <h3 class="text-muted my-5">Menu</h3>
            <table class="table">
                <thead class="table table-default">
                    <tr>
                        <th>品种</th>
                        <th>删除</th>
                    </tr>
                </thead>
                <tbody v-for="item in getMenuItems" :key="item.name">
                    <tr>
                        <td>{{item.name}}</td>
                        <td>
                            <button @click="deleteData(item)"   class="btn btn-outline-danger btn-sm">&times;</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

    </div>
</template>

<script>
    import NewPizza from './NewPizza.vue'
    export default {
        name: "Admin",
        // beforeRouteEnter:(to,from,next)=>{
            // alert("hello"+this.name);
            //结果是hello undefined 因为执行beforeRouteEnter时，并没有把数据渲染出来
            // next()   可以正常跳转
           //可以使用回调实现
        //     next(vm=>{
        //         alert("hello"+this.name);
        //     })
        // },
        // beforeRouteLeave(to,from,next){
        //   if(confirm("确定离开吗？") == true){
        //       next()
        //   }else{
        //       next(false)
        //   }
        // },
        components:{
            'newpizza':NewPizza
        },
        computed:{
            getMenuItems:{
                //在vuex中获取数据
                get(){
                    // return this.$store.state.menuItems
                    return this.$store.getters.getMenuItems
                },
                set(){

                }

            }
        },
        created(){
            fetch("https://wd3441785329pgyufa.wilddogio.com/menu.json")
            // this.http.get("menu.json")
                .then((res)=>{
                    return res.json();
                })
                .then((data)=>{
                    let menuArray =[];
                    for (let key in data){

                        data[key].id = key;
                        menuArray.push(data[key])
                    }
                    // this.getMenuItems = menuArray;
                    //数据同步
                    this.$store.commit("setMenuItems",menuArray)
                })


        },
        methods:{
            deleteData(item){
                fetch("https://wd3441785329pgyufa.wilddogio.com/menu/"+item.id+"/.json",{
                    method:"DELETE",
                    headers:{
                        "Content-type":"application/json"
                    },
                })
                    .then((res)=>{
                        return res.json()
                    })
                    .then((data)=>{
                      // this.$router.push({name:"menuLink"})
                        this.$store.commit("removeMenuItems",item)
                    })

                    .then((err)=>{
                        console.log(err);
                    })
            }
        }
    }
</script>

<style scoped>

</style>


