<template>
    <div class="row">
        <div id="home" class="col-sm-12 text-center">
            <div class="background">
                <h1>欢迎大家品尝pizza!</h1>
                <h2>这里有你喜爱的pizza</h2>
                <button @click="goToMenu" class="btn btn-success">Let's order!</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "home",
        methods: {
            goToMenu() {
                //跳转到上一次浏览的页面
                //this.$router.go(-1);
                //指定跳转的地址
                //this.$router.replace('/menu');
                //指定跳转的路由名
                //this.$router.replace({name:'adminLink'})
                //通过push进行跳转(常用)
                // this.$router.push('/menu');
                this.$router.push({name:'menuLink'})
            }
        }
    }
</script>

<style scoped>
    #home {
        background: url("../../static/img/pizza.jpg");
        height: 85vh;
        padding: 20%;
    }

    h1, h2 {
        margin: 6%;
    }
    .background{
        background: #eee;
        opacity: 0.8;
        max-width: 70vh;
        margin: 0 auto;
        padding: 20px 0;
    }
</style>


