import Home from '../components/Home'
import Menu from '../components/Menu'
import Admin from '../components/Admin'
import About from '../components/About/About'
import Login from '../components/Login'
import Register from '../components/Register'

//二级路由
import Contact from '../components/About/Contact/Contact'
import Delivery from '../components/About/Delivery'
import Guide from '../components/About/Guide'
import History from '../components/About/History'

//三级路由
import Personone from '../components/About/Contact/Personone'
import Persontwo from '../components/About/Contact/Persontwo'



export const routes = [
        {path: '/', name: 'homeLink',
            //路由复用
            components: {
            default:Home,
                'guide':Guide,
                'delivery':Delivery,
                'history':History
            }
            },
        {path: '/menu', component: Menu, name: 'menuLink'},
        {path: '/admin', component: Admin, name: 'adminLink',
        //路由独享守卫
        //     beforeEnter:(to,from,next)=>{
        //     alert("非登录状态，不能访问此页面");
        //     next(false);
        //     }
        },
        {
            path: '/about', component: About, name: 'aboutLink',redirect: '/about/contact',
            children: [
                {
                    path: '/about/contact', component: Contact, name: 'contactLink',redirect: '/about/contact/personone',
                    children: [
                        {path: '/about/contact/personone', component: Personone, name: 'personOne'},
                        {path: '/about/contact/persontwo', component: Persontwo, name: 'personTwo'}
                    ]
                },
                {path: '/about/delivery', component: Delivery, name: 'deliveryLink'},
                {path: '/about/history', component: History, name: 'historyLink'},
                {path: '/about/guide', component: Guide, name: 'guideLink'}
            ]
        },
        {path: '/login', component: Login, name: 'loginLink'},
        {path: '/register', component: Register, name: 'registerLink'},
        {path: '*', redirect: '/'}
    ]






